r button,.subscribe-newsletter input{height:42px;box-sizing:border-box}.subscribe-newsletter input{flex:6;border:none;width:100%;padding:15px;font-size:16px;font-size:var(--font-size);background:hsla(0,0%,100%,.1)}.subscribe-newsletter input:focus{outline:none!important}.subscribe-newsletter input.has-value,.subscribe-newsletter input:focus{background:#fff;background:var(--white)}.subscribe-newsletter input::-ms-input-placeholder{color:#959595;color:var(--warm-grey)}.subscribe-newsletter input::placeholder{color:#959595;color:var(--warm-grey)}.subscribe-newsletter button{align-self:flex-end;border:none;outline:none;width:45px;position:relative;transition:all .3s}.subscribe-newsletter .submit-button{background-color:#629ff4;background-color:var(--blue)}.subscribe-newsletter .submit-button .icon{height:12px}.subscribe-newsletter .success-button{background-color:#59cbb7;background-color:var(--valid-green)}.subscribe-newsletter .success-button .icon{height:25px}.subscribe-newsletter .error-button{background-color:#ff4f5e;background-color:var(--red)}.subscribe-newsletter .error-button .icon{height:24px}.subscribe-newsletter .icon{margin:0;position:absolute;top:50%;left:50%;transform:translate(-50%,-50%)}.subscribe-newsletter .icon path{fill:#fff}.subscribe-newsletter .checkbox-container{border-color:#fff;border-color:var(--white)}.subscribe-newsletter .checkbox-container path{fill:#fff;fill:var(--white)}