:root{--white:#fff;--black:#000;--red:#ff4f5e;--light-red:#fdc6c8;--green:#b7d43f;--valid-green:#59cbb7;--near-black:#4a4a4a;--grey:#e7e5e2;--darker-grey:#e6e5e3;--light-grey:#f3f2f0;--lighter-grey:#f9f9f9;--desert-storm:#f3f2f1;--warm-grey:#959595;--blue:#629ff4;--link-color:var(--blue);--override-z-index:3;--top-z-index:2;--middle-z-index:1;--bottom-z-index:0;--base-font-family:"Open Sans",sans-serif;--strong-font-family:"Zilla Slab",Georgia,Utopia,Charter,serif;--font-size:16px;--font-size-xs:12px;--font-size-lg:24px;--font-size-xl:32px;--font-size-xxl:34px;--standard-margin:0.3rem;--wide-desktop-width:1400px;--desktop-width:76rem;--old-desktop-width:55rem;--page-margin:1rem;--input-height:2.5rem;--spinner-height:10rem;--card-shadow:rgba(0,0,0,0.09) 0px 1px 6px,rgba(0,0,0,0.09) 0px 1px 4px;--transition-duration:0.2s;--transition-duration-slow:0.5s;--easing:cubic-bezier(0.4,0,0.2,1);--header-height:4rem;--nav-shadow-overhang:0px 2px 4px rgba(0,0,0,0.03);--dataset-item-margin:0.7rem;--profile-column-margin:30px}@keyframes fade-in{0%{opacity:0;pointer-events:none}to{opacity:1;pointer-events:all}}.md-block{display:none}@media (min-width:768px){.md-block{display:block}}@media (max-width:768px){.hidden-sm-down{display:none!important}}@media (max-width:992px){.hidden-md-down{display:none!important}}@media (min-width:768px){.hidden-md-up{display:none!important}}@media (min-width:992px){.hidden-lg-up{display:none!important}}.banner{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:center;flex-wrap:wrap;z-index:1;z-index:var(--middle-z-index);background:#000;background:var(--black);transform:translateY(0);transition:transform .2s cubic-bezier(.4,0,.2,1);transition:transform var(--easing) var(--transition-duration)}.banner>*{margin:20px}.banner h1{text-align:center}.banner .spacer{margin-right:auto}@media (max-width:768px){.banner .spacer{display:none}}.banner .cta{border:2px solid hsla(0,0%,100%,.5);border-radius:30px;margin-left:30px;min-width:150px;width:auto;background-color:transparent}.banner .close{border:none;margin-left:auto}.banner .close path{fill:#fff;fill:var(--white)}.notification-banner{position:fixed;top:0}.notification-banner h1,.notification-banner h1 a{background-image:linear-gradient(90deg,#f89096,#b1b4e5);color:transparent;font-size:32px;font-size:var(--font-size-xl);text-align:center;background-clip:text;-webkit-background-clip:text}.notification-banner h1 a{text-decoration:underline;border-bottom:2px solid #f89096}